import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../style/main.scss';
import ShowcaseProjects from '../components/Projects/ShowcaseProjects';
import { PortfolioProvider } from '../context/context';
import TitlePages from '../components/Title/TitlePages';
import FooterPages from '../components/Footer/FooterPages';

const ProjectShowcasePage = ({ pageTitle, pageDescription, projectData }) => {
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    setProjects([...projectData]);
  }, [projectData]);

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <html lang="en" />
        <meta name="description" content={pageDescription} />
      </Helmet>
      <PortfolioProvider value={{ projects }}>
        <TitlePages text={pageTitle} />
        <ShowcaseProjects projects={projects} />
        <FooterPages />
      </PortfolioProvider>
    </>
  );
};

ProjectShowcasePage.propTypes = {
  pageTitle: PropTypes.string.isRequired,
  pageDescription: PropTypes.string.isRequired,
  projectData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      img: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      info: PropTypes.string.isRequired,
      info2: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
      repo: PropTypes.string.isRequired,
    })
  ).isRequired,
};

export default ProjectShowcasePage;
